import axios from '@/plugins/axios.js'

const authModule = {
  namespaced: true,
  state: () => ({
    selectedRole: '',
    accessLoginToken: '',
    accessRoleToken: '',
    refreshToken: '',
    roles: [],
    authError: undefined,
    isLoading: undefined,
    isAuth: false, // todo: change to false
    authenticatedUserId: 1, // todo: change to undefined
  }),
  mutations: {
    initializeLoginData(state, data) {
      state.accessLoginToken = data.accessToken
      state.refreshToken = data.refreshToken
      state.roles = data.roles
      console.log(data)
    },
  },
  actions: {
    async verifyUserLogin({state, commit}, authData) {
      state.isLoading = true;
      await axios.post('/login/password', authData)
        .then(response => {
          commit('initializeLoginData', response.data);
          localStorage.setItem('login_token', state.accessLoginToken)
          state.authError = false
        })
        .catch(err => {
          state.authError = true
          throw err
        })
        .finally(() => {
          state.isLoading = false;
        })
    },
    async chooseRole({ state }, role) {
      const rolePostData = {
        accessToken: state.accessLoginToken,
        role: role.name,
        departmentId: role.departmentId
      }
      await axios.post('/login/role', rolePostData)
        .then(response => {
          state.accessRoleToken = response.data.roleToken
          localStorage.setItem('role_token', state.accessRoleToken)
          state.isAuth = true
        })
        .catch(err => {
          throw err
        })
    }
  },
  getters: {
    roles(state) {
      return state.roles
    },
    // todo: remove boolean interpreter
    isAuthStorage(){
      return !!localStorage.getItem('role_token')
    }
  }
}

export default authModule;