import NotFoundPage from '@/views/404'
import WelcomePage from '@/views/WelcomePage'

import store from '@/store/index'

import docsRoutes from './docs-routes';
import authRoutes from './auth-routes';

const routes = [{
    path: '*',
    component: NotFoundPage,
  },
  {
    path: '/',
    component: WelcomePage,
    beforeEnter: (to, from, next) => {
      if (localStorage.getItem('role_token')) {
        // todo: verification on the back-end
        next({
          path: '/home'
        })
      } else {
        next()
      }
    }
  },
  {
    path: '/home',
    name: 'home',
    component: () => import( /* webpackChunkName: "home-page" */ '@/views/Home.vue'),
    meta: {
      breadCrumb: [{
        text: 'Домашня Сторінка'
      }]
    }
  },
  {
    path: '/staff/',
    name: 'staff',
    component: () => import( /* webpackChunkName: "staff-page" */ '@/views/Staff/StaffList'),
  },
  {
    path: '/user/:id',
    name: 'user',
    component: () => import( /* webpackChunkName: "user-profile-page" */ '@/views/UserProfilePage'),
    meta: {
      breadCrumb: [{
          text: 'Домашня Сторінка',
          to: {
            name: 'home'
          },
          disabled: false
        },
        {
          text: 'Профіль',
          disabled: true
        }
      ]
    }
  },
  ...docsRoutes,
  ...authRoutes
]

export default routes;