import Vue from 'vue'
import Vuex from 'vuex'

import state from './state'
import mutations from './mutations'
import actions from './actions'
import getters from './getters'

import {
  auth,
  docs,
  individualPlan,
  userProfile
} from './modules/'

Vue.use(Vuex)

export default new Vuex.Store({
  state: state,
  mutations: mutations,
  actions: actions,
  getters: getters,
  modules: {
    auth,
    docs,
    individualPlan,
    userProfile
  },
})
