import fakeDocs from '../../docs-data/user-profile/fake-docs.js';

const userProfile = {
  namespaced: true,
  state: () => ({
    documents: [],
  }),
  mutations: {
    setDocuments(state, docs) {
      state.documents = docs;
    }
  },
  actions: {
    fetchDocs({ commit }) {
      return new Promise((resolve, reject) => {
        setTimeout(() => {
          commit('setDocuments', fakeDocs);
          resolve(fakeDocs);
        }, 1500)
      })
    }
  }
}

export default userProfile;