import {
  columns,
  uniqueColumns
} from '@/docs-data/indiv-plan-table/';

import { extendTableProps } from '@/utils/extendTableProps';

const individualPlan = {
  namespaced: true,
  state: () => ({
    columns: columns,
    uniqueColumns: uniqueColumns,
    rows: [],
  }),
  mutations: {
    addRow(state, row) {
      state.rows.push(row);
      this.commit('individualPlan/setExtendedRows', row);
      console.info('rows', state.rows)
    },
    extendRowData(state, { row, newObjectProp, value }) {
      if (!newObjectProp || typeof newObjectProp !== 'string') {
        throw Error('New object property has to be specified as string type!');
      }

      extendTableProps(row, newObjectProp, value);
      return Object.assign({}, row); 
    },
    setExtendedRows(state, row) {
      this.commit('individualPlan/extendRowData', {
        row, 
        newObjectProp: 'isEdited', 
        value: false 
      }) 
    },
  },
}

export default individualPlan;